// src/app/admin/layout.tsx
"use client";

import React, { useEffect, useState } from "react";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import axios, { AxiosError } from "axios";
import { Loader2 } from "lucide-react";

interface LayoutProps {
  children: React.ReactNode;
}

const API_BASE = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:5000";
const ME_URL = `${API_BASE}/api/admin/me`;
const LOGOUT_URL = `${API_BASE}/api/admin/logout`;
const REQUEST_TIMEOUT = 7000;

const AdminLayout: React.FC<LayoutProps> = ({ children }) => {
  const router = useRouter();
  const pathname = usePathname();

  const [loading, setLoading] = useState(true);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Verify admin session using cookie
  useEffect(() => {
    let mounted = true;

    const verifyAdmin = async () => {
      try {
        setLoading(true);
        const res = await axios.get(ME_URL, {
          withCredentials: true,
          timeout: REQUEST_TIMEOUT,
        });

        if (!mounted) return;

        if (res.data?.success) {
          setIsAuthenticated(true);
        } else {
          setIsAuthenticated(false);
          router.replace("/admin");
        }
      } catch {
        if (!mounted) return;
        setIsAuthenticated(false);
        router.replace("/admin");
      } finally {
        if (mounted) setLoading(false);
      }
    };

    verifyAdmin();

    return () => {
      mounted = false;
    };
  }, [router]);

  // Logout handler
  const handleLogout = async () => {
    try {
      setError(null);
      await axios.post(LOGOUT_URL, {}, { withCredentials: true, timeout: REQUEST_TIMEOUT });
      setIsAuthenticated(false);
      router.replace("/admin");
    } catch (err: unknown) {
      if (axios.isAxiosError(err)) {
        setError(err.response?.data?.message || "Logout failed");
      } else if (err instanceof Error) {
        setError(err.message);
      } else {
        setError("Logout failed");
      }
    }
  };

  // Loading screen
  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen text-gray-500">
        <Loader2 className="animate-spin mr-2" /> Checking admin session...
      </div>
    );
  }

  // Not authenticated â€” render nothing (redirect triggered)
  if (!isAuthenticated) return null;

  const navLinks = [
    { href: "/admin", label: "Dashboard" },
    { href: "/admin/jobs", label: "Jobs" },
    { href: "/admin/applications", label: "Applications" },
    { href: "/admin/promos", label: "Promos" },
    { href: "/admin/gallery", label: "Gallery" },
    { href: "/admin/testimonials", label: "Testimonials" },
  ];

  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Sidebar */}
      <aside className="w-64 bg-white shadow-lg flex flex-col justify-between">
        <div>
          <div className="p-6 border-b">
            <h1 className="text-2xl font-bold text-blue-600">Fahari Admin</h1>
          </div>
          <nav className="mt-4 flex flex-col">
            {navLinks.map(({ href, label }) => (
              <Link
                key={href}
                href={href}
                className={`px-6 py-3 hover:bg-gray-100 ${
                  pathname === href ? "bg-gray-200 font-semibold" : ""
                }`}
              >
                {label}
              </Link>
            ))}
          </nav>
        </div>
        <button
          onClick={handleLogout}
          className="m-4 px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600"
        >
          Logout
        </button>
      </aside>

      {/* Main content */}
      <main className="flex-1 p-6">
        {error && (
          <div className="mb-4 p-3 rounded bg-red-100 text-red-700 text-sm">{error}</div>
        )}
        {children}
      </main>
    </div>
  );
};

export default AdminLayout;
